/** @format */

.card {
  display: flex;
  flex-direction: column;
  width: 240px;
  @media (max-width: 767.98px) {
    width: 312px;
  }
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.2), 0px 3px 4px rgba(0, 0, 0, 0.12),
    0px 2px 4px rgba(0, 0, 0, 0.14);
  border-radius: 4px;
  transition: box-shadow 0.3s ease 0s, transform 0.3s ease 0s;
  margin: 0px 10px 24px;
  @media (max-width: 767.98px) {
    margin: 0px 10px 32px;
  }
  &:hover {
    box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.4), 0px 3px 4px rgba(0, 0, 0, 0.24),
      0px 2px 4px rgba(0, 0, 0, 0.28);
    transform: translateY(-5px);
  }
  &__image {
    position: relative;
    width: 240px;
    height: 168px;
    @media (max-width: 767.98px) {
      width: 312px;
      height: 232px;
    }
    img {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      object-fit: cover;
    }
  }
  &__info {
    margin: 16px;
    display: flex;
    flex-direction: column;
    flex: 1 1;
  }
  &__title {
    margin-bottom: 8px;
    font-weight: 500;
    font-size: 20px;
    flex: 1 1;
    a,
    a:visited {
      transition: all 0.3s ease 0s;
      color: rgba(0, 0, 0, 0.87);
      &:hover {
        opacity: 0.6;
      }
    }
  }
  &__text {
    a,
    a:visited {
      transition: all 0.3s ease 0s;
      color: rgba(0, 0, 0, 0.6);
      &:hover {
        opacity: 0.6;
      }
    }
  }
}
